import React  from 'react'
function Nosotros() {
    
    
    return (
        <div id="nosotros">
        <div id="nosotros1" className='animate__animated animate__fadeInLeft animate__delay-1s'>
        </div>
        <h1 className="h1-nosotros1 animate__animated animate__fadeInLeft animate__delay-1s">
            <span className="span-carta">La Rabieta</span> empezó a dar felicidad en 2020 </h1>
        
        <div id="nosotros2" className='animate__animated animate__fadeInLeft animate__delay-2s'>
        </div>
        <h1 className="h1-nosotros2 animate__animated animate__fadeInLeft animate__delay-3s">
            Con toda clase de cervezas artesanas y de cualquier tipo junto a una buena variedad de tapas <span className="span-carta">caseras</span>
        </h1>
        </div>
    )
}

export default Nosotros
