import React,{useState} from 'react'
import logogrande from '../images/logorabieta.jpg'
import {FaTimes, FaBars} from 'react-icons/fa'
function Navbar() {

    const [open, setOpen] = useState(false)

    const OpenClick = ()=>{
        setOpen(!open)

    }
    return (
        <nav className="navbar">
            <ul className="nav-links">
                <li className="logo-link">
                    <a href="/" className="logo-img"><img src={logogrande}/></a>
                </li>
                <li className="nav-link">
                    <a href="#main">Inicio</a>
                </li>
                <li className="nav-link">
                    <a href="#localizacion">Localizacion</a>
                </li>
                <li className="nav-link">
                    <a href="#nosotros">Sobre nosotros</a>
                </li>
                <li className="nav-link">
                    <a href="#carta">Carta</a>
                </li>
                <li className="nav-link">
                    <a href="#horario">Horario</a>
                </li>
                <li className="nav-menu">
                    <button className="nav-button" onClick={OpenClick}>{open?<FaTimes/>:<FaBars/>}</button>
                </li>
                
            </ul>
            <div className={open?'show animate__animated animate__fadeIn':'hidden'}>
                <a href="#main">Inicio</a>
                <a href="#localizacion">Localizacion</a>
                <a href="#carta">Carta</a>
                <a href="#horario">Horario</a>
            </div>
        </nav>
    )
}

export default Navbar
