import React from 'react'

function carta() {
    return (
        <div id="carta">
        <div id="carta1" className='animate__animated animate__fadeInLeft animate__delay-1s'>
        </div>
        <h1 className="h1-carta1 animate__animated animate__fadeInLeft animate__delay-1s">
            Nuestras tradicionales <span className="span-carta">Cervezas industriales</span> </h1>
        <div id="carta2" className='animate__animated animate__fadeInLeft animate__delay-2s'>
        </div>
        <h1 className="h1-carta2 animate__animated animate__fadeInLeft animate__delay-2s">
            Nuestras novedosas y deliciosas <span className="span-carta">Cervezas artesanales y en grifo </span></h1>
            
        </div>
    )
}

export default carta
